import {VdlError} from '../core';

/**
 * Exception thrown when cols property is missing from grid-list
 * @docs-private
 */
export class VdlGridListColsError extends VdlError {
  constructor() {
    super(`vdl-grid-list: must pass in number of columns. Example: <vdl-grid-list cols="3">`);
  }
}

/**
 * Exception thrown when a tile's colspan is longer than the number of cols in list
 * @docs-private
 */
export class VdlGridTileTooWideError extends VdlError {
  constructor(cols: number, listLength: number) {
    super(`vdl-grid-list: tile with colspan ${cols} is wider than grid with cols="${listLength}".`);
  }
}

/**
 * Exception thrown when an invalid ratio is passed in as a rowHeight
 * @docs-private
 */
export class VdlGridListBadRatioError extends VdlError {
  constructor(value: string) {
    super(`vdl-grid-list: invalid ratio given for row-height: "${value}"`);
  }
}
